<template>
    <div class="room-setting">
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>常规设置</span>
                    <div class="operate" @click="handleSave">修改/保存</div>
                </div>
            </div>
            <div class="data-con special">
                <div class="item-info">
                    <span>客服（微信）</span>
                    <i-input v-model="editService.wechat" class="column-input" placeholder="联系该网站者"></i-input>
                </div>
                <div class="item-info">
                    <Checkbox v-model="editService.autoScore"></Checkbox>
                    <span>虚拟玩家自动确认上下分请求&nbsp;&nbsp;确认随机时间</span>
                    <InputNumber :max="60" :min="1" class="base-item-input" :disabled="!editService.autoScore" v-model="editService.autoScoreTime"></InputNumber>
                    <span>秒</span>
                </div>
                <div class="item-info">
                    <span>客服（QQ）</span>
                    <i-input v-model="editService.qq" class="column-input" placeholder="联系该网站者"></i-input>
                </div>
                <div class="item-info">
                    <Checkbox v-model="editService.open800">开启800客服功能&nbsp;&nbsp;<i-input v-model="editService.address800" class="column-input" placeholder="请填写800接入URL"></i-input></Checkbox>
                </div>
                <div class="item-info">
                    <span>虚拟增加在线人数</span>
                    <InputNumber :max="100" :min="0" v-model="addPersonNum" class="column-input"></InputNumber>
                    <span>(最多可增加100人)</span>
                </div>
            </div>
        </div>
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>房间设置</span>
                    <div class="operate" @click="handleRoomSet">修改/保存</div>
                </div>
            </div>
            <div class="data-con">
                <div class="item-info">
                    <span>选择游戏</span>
                    <i-select v-model="roomSet.selectRoom" @on-change="getRoomData" style="width: 280px;text-align: left;">
                        <i-option v-for="(item, index) in gameList" :key="index" :value="item.key">{{item.name}}</i-option>
                    </i-select>
                </div>
                <div class="item-info">
                    <span>房间名称</span>
                    <i-input v-model="roomSet.name" class="column-input"></i-input>
                </div>
                <div class="item-info">
                    <span>房间开关</span>
                    <switchs :size="'big'" :open="roomSet.status" @switch="handleSwitch"></switchs>
                </div>
                <div class="item-info">
                    <span>房间号码</span>
                    <i-input :maxlength="6" v-model="roomSet.num" class="column-input"></i-input>
                    <span>（修改后首页房间号自动更新）</span>
                </div>
                <div class="item-info">
                    <span>最低进房积分</span>
                    <InputNumber :min="0" v-model="roomSet.minBalance" class="column-input"></InputNumber>
                </div>
                <div class="item-info">
                    <span>账单最低显示积分</span>
                    <InputNumber :min="1" v-model="roomSet.showMinMoney" class="column-input"></InputNumber>
                </div>
                <div class="column-item">
                    <span class="column-item-header">回水是否通知</span>
                    <RadioGroup v-model="roomSet.notice">
                        <Radio v-for="(item, index) in noticeList" :key="index" :label="item.key">
                            <span>{{item.title}}</span>
                        </Radio>
                    </RadioGroup>
                </div>
                <!-- <div class="column-item">
                    <span class="column-item-header">{{selectKey === 'lottery' ? '50秒后能否撤销' : '下注后能否撤销'}}</span>
                    <RadioGroup v-model="roomSet.withdraw">
                        <Radio v-for="(item, index) in withdrawList" :key="index" :label="item.key">
                            <span>{{item.title}}</span>
                        </Radio>
                    </RadioGroup>
                </div> -->
                <div class="column-item">
                    <span class="column-item-header">账单显示</span>
                    <RadioGroup v-model="roomSet.billOrder">
                        <Radio v-for="(item, index) in billOrderList" :key="index" :label="item.key">
                            <span>{{item.title}}</span>
                        </Radio>
                    </RadioGroup>
                </div>
                <div class="column-item">
                    <Checkbox v-model="roomSet.autoBackWater">启用自助返水</Checkbox>
                </div>
                <div class="column-item">
                    <span class="column-item-header">游戏飞单设置</span>
                    <RadioGroup v-model="roomSet.record">
                        <Radio v-for="(item, index) in flyList" :key="index" :label="item.key">
                            <span>{{item.title}}</span>
                        </Radio>
                    </RadioGroup>
                </div>
                <div v-show="roomSet.type == 2 && selectKey === 'lottery'" class="column-item">
                    <span class="column-item-header" style="width: 84px;">开和</span>
                    <RadioGroup v-model="roomSet.kaihe">
                        <Radio v-for="(item, index) in kaiheList" :key="index" :label="item.key">
                            <span>{{item.title}}</span>
                        </Radio>
                    </RadioGroup>
                </div>
                <div v-show="roomSet.type == 2 && selectKey === 'video'" class="column-item">
                    <span class="column-item-header" style="width: 84px;">下注庄闲开和</span>
                    <RadioGroup v-model="roomSet.kaihe">
                        <Radio v-for="(item, index) in kaiheList1" :key="index" :label="item.key">
                            <span>{{item.title}}</span>
                        </Radio>
                    </RadioGroup>
                    <span>注:退一半算一半金额的流水，通杀全算流水，全退无流水</span>
                </div>
                <div class="column-item" style="margin-right: 105px">
                    <Checkbox v-model="roomSet.Revoke">游戏全程禁止取消</Checkbox>
                </div>
                <div class="column-item">
                    <Checkbox v-model="roomSet.Instructions">隐藏玩家APP赔率说明</Checkbox>
                </div>
                <div v-if="false" v-show="roomSet.type == 2 && selectKey === 'video'" class="column-item">
                    <Checkbox v-model="roomSet.zxbjls">庄/闲对冲下注不计入玩家流水</Checkbox>
                </div>
                <div v-if="false" v-show="roomSet.type == 2 && selectKey === 'video'" class="column-item">
                    <Checkbox v-model="roomSet.dxbjls">大/小对冲下注不计入玩家流水</Checkbox>
                </div>
                <div v-if="false" v-show="roomSet.type == 2 && selectKey === 'video'" class="column-item">
                    <Checkbox v-model="roomSet.bjsdx">每靴30局以后不接受大/小玩法下注</Checkbox>
                </div>
                <div v-if="false" v-show="roomSet.type == 2 && selectKey === 'video'" class="column-item">
                    <Checkbox v-model="roomSet.z6win">当庄赢且为6点时，庄赔率为<InputNumber :min="0" :step=".1" :precision="1" v-model="roomSet.z6winpl" class="column-input" style="width: 80px"></InputNumber></Checkbox>
                </div>
            </div>
            <div class="data-con" style="margin-top: 0;">
                <div class="column-item" style="margin-right: 42px">
                    <Checkbox v-model="roomSet.HaltSales">启用自动开/停，停售时间&nbsp;&nbsp;<Time-picker format="HH:mm" placeholder="停售时间" style="width: 80px" v-model="roomSet.HaltTime"></Time-picker>&nbsp;&nbsp;开售时间&nbsp;&nbsp;<Time-picker format="HH:mm" placeholder="开售时间" style="width: 80px" v-model="roomSet.OnSaleTime"></Time-picker></Checkbox>
                </div>
            </div>
        </div>
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>上分收款账户设置</span>
                </div>
            </div>
            <div class="data-con">
                <div class="data-item" v-for="(item, index) in subAccData" :key="index">
                    <div class="item-left">
                        <img :src="item.qrCode || require('../../assets/roomSetting/qrcode.png')" alt title />
                        <div v-show="!item.qrCode" class="no-qrcode">
                            <span>未上传二维码</span>
                        </div>
                    </div>
                    <div class="item-right">
                        <img :src="item.accountType === '1' ? require('../../assets/roomSetting/alipay.jpg') : require('../../assets/roomSetting/wechat.png')" alt title/>
                        <p :title="item.accountNum">{{'账号：'+item.accountNum}}</p>
                        <div class="modify" @click="handleModifyAccount(item)">修改</div>
                    </div>
                </div>
                <div class="data-item">
                    <div class="item-left">
                        <img :src="AdminPortrait || require('../../assets/roomSetting/contactService_bak.png')" alt title />
                    </div>
                    <div class="item-right">
                        <p style="font-size: 22px;height: 100px;line-height: 120px;padding-left: 0;">管理员头像</p>
                        <div class="modify" @click="handleModifyAccount(null)">修改</div>
                    </div>
                </div>
            </div>
        </div>
        <!--<Collapse v-model="addMessage.value">
            <Panel name="1">
                <div class="room-header">
                    <span>推送信息</span>
                    <i-button @click.stop="handleSend" style="height: 30px;">推送</i-button>
                </div>
                <div slot="content" class="limit-content">
                    <div class="column-item">
                        <span class="column-item-header">推送信息</span>
                        <i-input v-model="addMessage.info" class="column-input"></i-input>
                    </div>
                </div>
            </Panel>
        </Collapse>-->
        <div v-show="accountSettingShow" class="account-setting-mask">
            <div v-show="accountSettingShow" class="account-setting" :style="{height: modifyHeader ? '230px' : '340px'}">
                <div class="account-setting-header">
                    <span v-text="modifyHeader ? '修改管理员头像' : '修改上分账户信息'"></span>
                    <span class="modal-close" @click="handleCloseModal">X</span>
                </div>
                <div class="account-setting-list">
                    <div v-show="!modifyHeader" class="account-setting-item">
                        <p class="columns-item-label">账号类型</p>
                        <i-input disabled v-model="account.type" class="columns-item-input"></i-input>
                    </div>
                    <div v-show="!modifyHeader" class="account-setting-item">
                        <p class="columns-item-label">收款帐号</p>
                        <i-input v-model="account.num" class="columns-item-input"></i-input>
                    </div>
                    <div class="account-setting-item">
                        <p class="columns-item-label" v-text="modifyHeader ? '管理员头像' : '收款图片'"></p>
                        <div class="columns-item-input upload-img" @click="handleUploadImg">
                            <img v-if="showUploadImg || account.img" :src="showUploadImg || account.img" alt="收款图片"/>
                            <p>点击上传图片</p>
                        </div>
                    </div>
                    <div class="account-setting-item">
                        <i-button @click="handleModify">提交修改</i-button>
                    </div>
                </div>
            </div>
        </div>
        <input v-show="false" ref="uploadImg" type="file"/>
        <!-- <loading :loading="showLoading"></loading> -->
    </div>
</template>

<script>
import switchs from '../../component/switch';
export default {
    components: {
        switchs
    },
    data() {
        return {
            showLoading: false,
            accountSettingShow: false,
            editServiceValue: '1',
            addMessage: {
                value: '1',
                info: ''
            },
            editService: {
                id: '',
                qq: '',
                wechat: '',
                autoScore: false,
                autoScoreTime: 15,
                openTime: true,
                time: 15,
                open800: true,
                address800: ''
            },
            addPersonValue: '1',
            addPersonNum: 10,
            roomSetValue: '',
            AdminPortrait: '',
            roomSet: {
                id: '',
                selectRoom: '',
                name: '',
                num: '',
                status: false,
                minBalance: 0,
                withdraw: 'true',
                notice: 'true',
                returnQuery: [],
                showMinMoney: 1,
                billOrder: 'sign',
                proxyAccount: '',
                proxyPassword: '',
                kaihe: '',
                autoBackWater: false,
                type: 0,
                zxbjls: true,
                dxbjls: true,
                bjsdx: true,
                z6win: true,
                z6winpl: 1.5,
                record: 1,
                Instructions: false,
                Revoke: false,
                HaltSales: false,
                HaltTime: '',
                OnSaleTime: ''
            },
            account: {
                type: '',
                num: '',
                img: ''
            },
            uploadImg: '',
            subAccountValue: '1',
            subAccData: [],
            flyList: [
                {
                    title: '不飞单',
                    key: 1
                },
                // {
                //     title: '飞单到高级商户',
                //     key: 2
                // },
                {
                    title: '飞单到外部网盘',
                    key: 3
                }
            ],
            billOrderList: [
                {
                    title: '单排',
                    key: 'sign'
                },
                {
                    title: '双排',
                    key: 'double'
                }
            ],
            kaiheList: [
                {
                    title: '返还本金',
                    key: 1
                },
                {
                    title: '通杀龙虎',
                    key: 2
                }
            ],
            kaiheList1: [
                {
                    title: '全退',
                    key: 1
                },
                {
                    title: '退一半',
                    key: 2
                },
                {
                    title: '通杀',
                    key: 3
                }
            ],
            queryList: [
                {
                    title: '积分',
                    key: 'integral',
                    value: 3
                },
                {
                    title: '输赢',
                    key: 'winningLosing',
                    value: 5
                },
                {
                    title: '流水',
                    key: 'flowingWater',
                    value: 4
                },
                {
                    title: '投注',
                    key: 'betting',
                    value: 1
                },
                {
                    title: '使用',
                    key: 'use',
                    value: 2
                }
            ],
            withdrawList: [
                {
                    title: '能',
                    key: 'true'
                },
                {
                    title: '否',
                    key: 'false'
                }
            ],
            noticeList: [
                {
                    title: '是',
                    key: 'true'
                },
                {
                    title: '否',
                    key: 'false'
                }
            ],
            roomStatusList: [
                {
                    title: '开启',
                    key: 'open'
                },
                {
                    title: '关闭',
                    key: 'close'
                }
            ],
            gameList: [],
            isLoading: false,
            uploadImg: '',
            showUploadImg: '',
            isSending: false,
            modifyHeader: false
        }
    },
    computed: {
        selectKey() {
            let that = this;

            return that.roomSet.selectRoom ? that.gameList.find(ele => ele.key === that.roomSet.selectRoom).nickName : '';
        }
    },
    mounted() {
        let that = this;
        that.getGameList().then(function() {
            // that.gameList.push({
            //     gameType: 2,
            //     key: "bjl",
            //     name: "百家乐",
            //     nickName: "video",
            //     type: 1
            // })
            setTimeout(function() {
                that.getData().then(function() {
                    setTimeout(function() {
                        that.getRoomData(that.gameList[0].key);
                    }, 1000)
                });
            }, 1000)
        })
    },
    methods: {
        getGameList() {
            let that = this;

            return new Promise((resolve, reject) => {
                that.$axios({
                    url: '/api/Merchant/GetGameList'
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.gameList = result.map(item => {
                            return {
                                name: item.GameName,
                                type: item.GameType,
                                key: item.NickName,
                                gameType: item.Type,
                                nickName: 'lottery'
                            }
                        }).sort((a, b) => a.type - b.type);
                        resolve();
                    }
                    reject();
                })
            })
        },
        handleSend() {
            let that = this;

            if (!that.addMessage.info) {
                that.$Message.error('请输入推送信息');
                return;
            }

            if (!that.isSending) {
                that.isSending = true;

                that.$axios({
                    url: '/api/Setup/AddArticle',
                    method: 'post',
                    data: {
                        ArticleType: 2,
                        Content: that.addMessage.info
                    }
                }).then(data => {
                    that.isSending = false;

                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.addMessage.info = '';
                })
            }
        },
        handleSave() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Room/UpdateRoomCus',
                    method: 'post',
                    params: {
                        roomID: that.editService.id,
                        qq: that.editService.qq,
                        weChat: that.editService.wechat,
                        ShamOnfirm: that.editService.autoScore,
                        ShamOnfirmTime: that.editService.autoScoreTime
                    }
                }).then(function(data) {
                    that.isLoading = false;
                    that.handleUpdateCustomUrl().then(function() {
                        that.handleAddPer();
                    })
                })
            }
        },
        handleUpdateCustomUrl() {
            let that = this;
            return new Promise((resolve, reject)  => {
                that.$axios({
                    url: '/api/Room/UpdateCustomerUrl',
                    method: 'post',
                    params: {
                        roomID: that.editService.id,
                        url: that.editService.address800,
                        status: that.editService.open800
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        resolve();
                    }
                })
            })
        },
        handleAddPer() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Room/UpdateRoomOnline',
                    method: 'post',
                    params: {
                        roomID: that.editService.id,
                        num: that.addPersonNum
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.isLoading = false;
                    that.getData();
                })
            }
        },
        handleRoomSet() {
            let that = this;

            if (that.roomSet.num.length < 6) {
                that.$Message.error('房间号码必须输入6位数');
                return;
            }

            that.handleRoomSetInfo();
        },
        handleSwitch() {
            let that = this,
                res = null,
                href = '';

            if (!that.isLoading) {
                that.isLoading = true;
                res = that.gameList.find(ele => ele.key === that.roomSet.selectRoom);

                switch(res.nickName) {
                    case 'lottery':
                        href = '/api/Room/SwitchGames';
                        break;
                    case 'video':
                        href = '/api/Room/SwitchVideoGames';
                        break;
                }

                that.$axios({
                    url: href,
                    params: {
                        gameID: that.roomSet.id,
                        type: res.type
                    }
                }).then(function(data) {
                    that.isLoading = false;
                    if (data.data.Status === 100) {
                        that.getRoomData();
                    }
                })
            }
        },
        handleRoomSetInfo() {
            return new Promise((resolve, reject) => {
                let that = this,
                    res = null,
                    href = '';

                if (!that.isLoading) {
                    that.isLoading = true;
                    res = that.gameList.find(ele => ele.key === that.roomSet.selectRoom);

                    switch(res.nickName) {
                        case 'lottery':
                            href = '/api/Room/UpdateGameofRoomInfo';
                            break;
                        case 'video':
                            href = '/api/Room/UpdateVideoGameofRoomInfo';
                            break;
                    }

                    that.$axios({
                        url: href,
                        method: 'post',
                        data: {
                            ID: that.roomSet.id,
                            GameRoomName: that.roomSet.name,
                            GameRoomNum: that.roomSet.num,
                            Status: that.roomSet.status ? 1 : 2,
                            Minin: that.roomSet.minBalance,
                            Che: that.roomSet.withdraw === 'true' ? true : false,
                            Back: that.roomSet.notice === 'true' ? true : false,
                            RInfoItems: that.queryList.map(item => {return {
                                Index: item.value,
                                Open: that.roomSet.returnQuery.findIndex(opt => {return opt === item.value}) !== -1
                            }}),
                            MinimumAmount: that.roomSet.showMinMoney,
                            Bill: that.roomSet.billOrder === 'sign' ? 1 : 2,
                            AgentAccount: that.roomSet.proxyAccount,
                            AgentPassword: that.roomSet.proxyPassword,
                            KaiEquality: that.roomSet.kaihe,
                            SwitchBackwater: that.roomSet.autoBackWater,
                            LotteryRecord: res.nickName == 'lottery' ? that.roomSet.record : 1,
                            VideoRecord: res.nickName == 'video' ? that.roomSet.record : 1,
                            Revoke: that.roomSet.Revoke,
                            Instructions: that.roomSet.Instructions,
                            HaltSales: that.roomSet.HaltSales,
                            HaltTime: new Date().pattern('yyyy-MM-dd') + ' ' + that.roomSet.HaltTime,
                            OnSaleTime: new Date().pattern('yyyy-MM-dd') + ' ' + that.roomSet.OnSaleTime
                        },
                        params: {
                            gameType: res.type
                        }
                    }).then(function(data) {
                        that.isLoading = false;
                        if (data.data.Status === 100) {
                            that.$Message.success(data.data.Message);
                            that.getRoomData(res.key);
                        } else {
                            that.$Message.error(data.data.Message);
                        }
                        resolve();
                    })
                }
            })
        },
        handleModifyAccount(item) {
            let that = this;

            if (item) {
                if (that.account.type !== item.accountType) {
                    that.showUploadImg = '';
                }
                that.account.type = item.accountType === '1' ? '支付宝' : '微信';
                that.account.num = item.accountNum;
                that.modifyHeader = false;
                that.account.img = item.qrCode;
            } else {
                that.showUploadImg = that.AdminPortrait || require('../../assets/roomSetting/contactService_bak.png');
                that.modifyHeader = true;
                that.account.type = 'admin';
            }
            that.accountSettingShow = true;
        },
        handleCloseModal() {
            let that = this;

            that.showUploadImg = '';
            that.modifyHeader = false;
            that.accountSettingShow = false;
        },
        handleModify() {
            let that = this;

            switch(that.account.type) {
                case '支付宝':
                    that.handleModifyPay('/api/Room/UpdateRoomAlipayPayment').then(function() {
                        that.getData();
                    });
                break;
                case '微信':
                    that.handleModifyPay('/api/Room/UpdateRoomWeChatPayment').then(function() {
                        that.getData();
                    });
                break;
                case 'admin':
                    that.handleModifyPay('/api/Room/UpdateAdminPortrait').then(function() {
                        that.getData();
                    });
                break;
            }
        },
        handleModifyPay(path) {
            return new Promise((resolve, reject) => {
                let that = this,
                    formData = new FormData();

                if (!that.uploadImg && that.account.type == 'admin') {
                    that.$Message.error('请选择上传图片');
                    return;
                }

                if (that.uploadImg && that.uploadImg.size > 5 * 1024 * 1024) {
                    that.$Message.error('上传图片不能超过5M');
                    return;
                }

                if (!that.isLoading) {
                    that.isLoading = true;

                    if (that.uploadImg) {
                        formData.append('fileinput', that.uploadImg);
                    }

                    that.$axios({
                        url: path,
                        method: 'post',
                        data: formData,
                        params: {
                            roomID: that.editService.id,
                            weChat: that.account.num,
                            alipay: that.account.num
                        }
                    }).then(function(data) {
                        if (data.data.Status === 100) {
                            that.$Message.success(data.data.Message);
                        } else {
                            that.$Message.error(data.data.Message);
                        }

                        that.showUploadImg = '';
                        that.uploadImg = null;
                        that.accountSettingShow = false;
                        that.isLoading = false;
                        resolve();
                    })
                    .catch(function(e) {
                        that.isLoading = false;
                        that.$Message.error(e);
                        reject(e)
                    })
                }
            })
        },
        handleUploadImg() {
            let that = this;
            that.$refs['uploadImg'].onchange = function(e) {
                that.uploadImg = e.target.files[0];

                let URL = window.URL || window.webkitURL;
                that.showUploadImg = URL.createObjectURL(that.uploadImg);
                that.$refs['uploadImg'].value = '';
            }
            that.$refs['uploadImg'].click();
        },
        handleChangeRoom(item) {
            let that = this;

            that.getRoomSetInfo(item);
        },
        getRoomData(type) {
            let that = this,
                res = null,
                href = '';

            if (!that.isLoading) {
                that.isLoading = true;
                that.showLoading = true;
                if (type) {
                    res = that.gameList.find(ele => ele.key === type);
                } else {
                    res = that.gameList.find(ele => ele.key === that.roomSet.selectRoom);
                }

                switch(res.nickName) {
                    case 'lottery':
                        href = '/api/Room/GetGameOfRoomInfo';
                        break;
                    case 'video':
                        href = '/api/Room/GetVideoGameOfRoomInfo';
                        break;
                }

                that.$axios({
                    url: href,
                    params: {
                        gameType: res.type
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Model;

                        that.roomSet = {
                            id: result.ID,
                            selectRoom: res.key,
                            name: result.GameRoomName,
                            num: result.GameRoomNum,
                            status: result.Status === 1 ? true : false, // 状态  1：开启  2：关闭
                            minBalance: result.Minin || 0,
                            withdraw:  result.Che.toString(),
                            notice: result.Back.toString(),
                            returnQuery: result.RInfoItems ? result.RInfoItems.filter(item => {return item.Open}).map(item => {return item.Index}) : [],
                            showMinMoney: result.MinimumAmount || 0,
                            billOrder: result.Bill === 1 ? 'sign' : 'double',
                            proxyAccount: result.AgentAccount,
                            proxyPassword: '',
                            kaihe: result.KaiEquality || 1,
                            autoBackWater: result.SwitchBackwater,
                            type: that.gameList.find(ele => ele.key == res.key).gameType,
                            record: res.nickName == 'lottery' ? result.LotteryRecord : result.VideoRecord,
                            Revoke: result.Revoke,
                            Instructions: result.Instructions,
                            HaltSales: result.HaltSales,
                            HaltTime: new Date(result.HaltTime).pattern('HH:mm'),
                            OnSaleTime: new Date(result.OnSaleTime).pattern('HH:mm')
                        }
                    }
                    that.isLoading = false;
                    that.showLoading = false;
                    that.getRoomDataStatus();
                    that.getRoomNum();
                })
            }
        },
        getRoomDataStatus() {
            let that = this;

            that.$axios({
                url: '/api/Room/GetGameIDs'
            }).then(function(data) {
                if (data.data.Status === 100) {
                    const result = data.data.Data,
                        res = result.find(item => item.ID === that.roomSet.id);

                    if (res) {
                        that.roomSet.status = res.Status === 1 ? true : false;
                    }
                }
            })
        },
        getRoomNum() {
            let that = this;

            that.$axios({
                url: '/api/Room/GetRoomNum'
            }).then(function(data) {
                if (data.data.Status === 100) {
                    that.roomSet.num = data.data.Model;
                }
            })
        },
        getData() {
            return new Promise((resolve, reject) => {
                let that = this;

                if (!that.isLoading) {
                    that.isLoading = true;
                    that.showLoading = true;

                    that.$axios({
                        url: '/api/Room/GetRoomInfo'
                    }).then(function(data) {
                        if (data.data.Status === 100) {
                            const result = data.data.Model;

                            that.editService = {
                                id: result.ID,
                                qq: result.QQ,
                                wechat: result.WeChat,
                                autoScore: result.ShamOnfirm,
                                autoScoreTime: result.ShamOnfirmTime,
                                address800: result.CustomerUrl,
                                open800: result.CustomerOpen
                            }

                            that.addPersonNum = result.Online;
                            that.AdminPortrait = result.AdminPortrait;

                            that.subAccData = [];
                            let aliCode = result.SubAccount.AlipayQRcode;
                            let wechatCode = result.SubAccount.WeChatQRcode;
                            that.subAccData.push({
                                accountType: '1',
                                accountNum: result.SubAccount.AlipayNum || '无',
                                qrCode: aliCode ? (aliCode.indexOf('://') === -1 ? ('/' + aliCode) : aliCode) : ''
                            }); // 支付宝

                            that.subAccData.push({
                                accountType: '2',
                                accountNum: result.SubAccount.WeChatNum || '无',
                                qrCode: wechatCode ? (wechatCode.indexOf('://') === -1 ? ('/' + wechatCode) : wechatCode) : ''
                            }); // 微信
                        }
                        that.isLoading = false;
                        that.showLoading = false;
                        resolve();
                    })
                }
            })
        }
    },
}
</script>

